html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
  background-color: rgb(245, 245, 245);
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 100rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.dark-mode {
  background-color: rgb(22, 22, 22);
}
.dark-mode .contenedor-sm .formulario {
  background-color: black;
}
.dark-mode .contenedor-sm .formulario input {
  background-color: rgb(173, 173, 173);
  color: black;
}
.dark-mode .tasks {
  background-color: rgb(22, 22, 22);
}
.dark-mode .tasks h2 {
  color: white;
}
.dark-mode .tasks .sidebar {
  background-color: black;
}
.dark-mode .tasks .sidebar a:hover {
  color: #193669;
  background-color: white;
}
.dark-mode .tasks .sidebar .activo {
  background-color: white;
  color: #193669;
}
.dark-mode .tasks .sidebar .luna {
  filter: none;
  transition: filter 0.5s ease;
}
.dark-mode .tasks .sidebar .luna:hover {
  cursor: pointer;
  filter: saturate(0);
  filter: brightness(100);
}
.dark-mode .tasks .contenido-header {
  background-color: black;
}
.dark-mode .tasks .contenido-header .titulo-header {
  background: rgb(226, 204, 65);
  background: linear-gradient(0deg, rgb(226, 204, 65) 0%, rgb(171, 10, 10) 100%);
  color: transparent;
  background-clip: text;
  font-size: 5rem;
  text-align: center;
  margin: 0;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .dark-mode .tasks .contenido-header .titulo-header {
    font-size: 8rem;
  }
}
@media (min-width: 768px) {
  .dark-mode .tasks .contenido-header .titulo-header {
    font-size: 3rem;
  }
}
.dark-mode .tasks .formulario, .dark-mode .tasks .modal {
  background-color: black;
}
.dark-mode .tasks .formulario label, .dark-mode .tasks .modal label {
  color: rgba(255, 255, 255, 0.8705882353);
}
.dark-mode .tasks .formulario input, .dark-mode .tasks .modal input {
  background-color: rgb(207, 207, 207);
}
.dark-mode .tasks .formulario .boton-perfil, .dark-mode .tasks .formulario .boton-crear, .dark-mode .tasks .modal .boton-perfil, .dark-mode .tasks .modal .boton-crear {
  background: linear-gradient(0deg, rgb(157, 19, 19) 0%, rgb(170, 170, 28) 100%);
}
.dark-mode .tasks .tareas {
  background-color: black;
}
.dark-mode .tasks .tareas p {
  color: white;
}
.dark-mode .footer {
  background-color: black;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.descripcion-pagina {
  text-align: center;
  margin: 2rem auto;
  color: #106276;
  font-weight: bold;
  font-size: 2.3rem;
}

.tagline {
  text-align: center;
  margin-bottom: 5rem;
  font-family: "Inter", sans-serif;
  font-weight: bold;
  font-size: 2.5rem;
}

.formulario {
  background-color: #FFFFFF;
  display: flex;
  flex-direction: column;
  gap: 3rem;
  padding: 4rem;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px -50px 36px -28px inset;
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .formulario {
    margin-bottom: 10rem;
  }
}
.formulario .campo {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.formulario .campo label {
  text-transform: uppercase;
  font-weight: bold;
  color: #6b7280;
}
.formulario .campo input {
  padding: 1rem;
  border: none;
  border-bottom: 1px solid gray;
}
.formulario .boton {
  align-self: center;
  background-color: #193669;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .formulario .boton {
    width: auto;
  }
}
.formulario .boton:hover {
  background-color: #0f2140;
  cursor: pointer;
}
.formulario .boton-crear {
  align-self: center;
  background-color: #193669;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .formulario .boton-crear {
    width: auto;
  }
}
.formulario .boton-crear:hover {
  background-color: #0f2140;
  cursor: pointer;
}

.acciones {
  font-size: 1.8rem;
  display: flex;
  flex-direction: column;
  text-align: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .acciones {
    flex-direction: row;
    justify-content: space-around;
  }
}
.acciones a {
  color: gray;
  font-weight: bold;
  transition: all 0.5s ease;
  margin-bottom: 4rem;
}

a:hover {
  color: black;
}

.alertas {
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  text-align: center;
  padding: 0.5rem 0;
  border-radius: 5px;
}

.error {
  background-color: rgb(219, 89, 89);
  background: linear-gradient(0deg, rgb(219, 89, 89) 0%, rgb(216, 22, 22) 100%);
}

.exito {
  background-color: rgb(89, 197, 47);
  background: linear-gradient(0deg, rgb(89, 197, 47) 0%, rgb(1, 35, 2) 100%);
}

.contenido-header {
  background-color: rgba(255, 255, 255, 0.671);
  margin: 1rem;
  border-radius: 8px;
  align-items: center;
  padding: 2rem 2rem;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
.contenido-header .titulo-header {
  background: rgb(154, 65, 226);
  background: linear-gradient(0deg, rgb(154, 65, 226) 0%, rgb(3, 156, 92) 100%);
  color: transparent;
  background-clip: text;
  font-size: 5rem;
  text-align: center;
  margin: 0;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .contenido-header .titulo-header {
    font-size: 8rem;
  }
}
@media (min-width: 768px) {
  .contenido-header .titulo-header {
    font-size: 3rem;
  }
}

.footer {
  background-color: #193669;
  height: 10rem;
  display: flex;
  align-items: center;
  justify-content: end;
  text-transform: uppercase;
  font-weight: bold;
}
.footer a {
  color: white;
  transition: all 0.5s ease;
  margin-right: 2rem;
}
.footer a:hover {
  color: rgb(234, 234, 67);
  background-color: #050c17;
  padding: 1rem;
  border-radius: 5px;
}

.sidebar {
  background-color: #193669;
  text-align: center;
  padding: 3rem;
}
.sidebar h2 {
  color: #FFFFFF;
  margin: 0;
}
@media (min-width: 768px) {
  .sidebar h2 {
    margin-bottom: 3rem;
  }
}
.sidebar .iconos-menu {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .sidebar .iconos-menu {
    display: block;
  }
}
.sidebar .ocultar {
  opacity: 0;
  height: 0;
  overflow: hidden;
}
.sidebar .icon {
  display: block;
  margin: 0 auto;
  cursor: pointer;
  margin: 0;
}
@media (min-width: 768px) {
  .sidebar .icon {
    display: none;
  }
}
.sidebar .luna {
  filter: saturate(0);
  filter: brightness(100);
  transition: filter 0.5s ease;
}
.sidebar .luna:hover {
  cursor: pointer;
  filter: none;
}
.sidebar .navegacion {
  transition: all 1s ease-in-out;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  text-transform: uppercase;
  font-weight: bold;
}
@media (min-width: 768px) {
  .sidebar .navegacion {
    opacity: 1;
    height: auto;
  }
}
.sidebar .navegacion a {
  color: white;
  transition: padding 0.5s ease;
}
.sidebar .navegacion a:hover, .sidebar .navegacion a.activo {
  color: rgb(234, 234, 67);
  background-color: #050c17;
  padding: 1rem;
  border-radius: 5px;
}

.borrar,
.pendiente,
.terminada {
  background-color: #b54435;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  cursor: pointer;
  border: none;
  padding: 0.5rem;
