.tasks .principal {
  flex: 1;
  position: relative;
  overflow: scroll;
  scrollbar-width: none;
}
.tasks .contenido {
  padding: 2rem;
  text-align: center;
}
.tasks .contenido .nombre-pagina {
  font-size: 2.8rem;
}

.listado-proyecto {
  list-style: none;
  padding: 2rem 0;
  background-color: white;
  border-radius: 10px;
  text-align: start;
  box-shadow: rgba(0, 0, 0, 0.35) 0px -50px 36px -28px inset;
}
.listado-proyecto .proyecto {
  margin: 5rem 2rem;
  font-weight: bold;
  font-size: 2rem;
  text-transform: capitalize;
  transition: all 0.5s ease;
}
.listado-proyecto .proyecto:hover {
  background-color: rgb(204, 202, 202);
  padding: 0.5rem;
  border-radius: 5px;
  cursor: pointer;
}

.contenedor-tareas {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  gap: 3rem;
}
.contenedor-tareas .agregar-tarea {
  background-color: #193669;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .contenedor-tareas .agregar-tarea {
    width: auto;
  }
}
.contenedor-tareas .agregar-tarea:hover {
  background-color: #0f2140;
  cursor: pointer;
}

.tareas {
  background-color: white;
  border-radius: 5px;
  padding: 2rem;
}
.tareas .tarea {
  display: block;
  border: 1px solid gray;
  padding-bottom: 1rem;
  font-weight: bold;
}
@media (min-width: 768px) {
  .tareas .tarea {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: none;
  }
}
.tareas .tarea p {
  cursor: pointer;
}

.contenedor-modal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.315);
  backdrop-filter: blur(2px);
}
.contenedor-modal .modal {
  position: relative;
  top: 0rem;
  left: 15%;
  opacity: 0.2;
  transition: all 0.6s ease-in;
  background-color: white;
  width: 80%;
  padding: 3rem 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.contenedor-modal .modal.mostrar {
  opacity: 1;
  top: 50%;
  transform: translateY(-50%);
}
.contenedor-modal .modal input {
  width: 90%;
  padding: 1rem;
  margin-right: 1rem;
  border: none;
  border-bottom: 1px solid gray;
}
.contenedor-modal .modal .modal-botones {
  display: flex;
  gap: 3rem;
}
.contenedor-modal .modal .modal-botones button {
  background-color: #193669;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 10px;
}
@media (min-width: 768px) {
  .contenedor-modal .modal .modal-botones button {
    width: auto;
  }
}
.contenedor-modal .modal .modal-botones button:hover {
  background-color: #0f2140;
  cursor: pointer;
}